/*  БАЗОВЫЕ НАСТРОЙКИ  */
body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img,
abbr {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
legend {
  color: #212121;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}
img {
  max-width: 100%;
  display: block;
  height: auto;
}
mark {
  background: none;
  color: inherit;
}
html {
  font-size: 14px;
}
@media (min-width: 560px) {
  html {
    font-size: 16px;
  }
}
@media (min-width: 990px) {
  html {
    font-size: 18px;
  }
}
@media (min-width: 1250px) {
  html {
    font-size: 20px;
  }
}
body {
  font-family: Calibri, Tahoma, sans-serif;
  line-height: 1.3;
  font-size: 14px;
  color: #212121;
  font-weight: 400;
  font-style: normal;
}
@media (min-width: 780px) {
  body {
    font-size: 16px;
  }
}
* {
  box-sizing: border-box;
  max-height: 100000px;
}
a {
  color: inherit;
  outline: none;
  text-decoration: none;
}
button,
a[role="button"] {
  cursor: pointer;
}
strong {
  font-weight: 700;
}
.main {
  min-height: 80vh;
  min-width: 320px;
}
.container {
  position: relative;
  z-index: 1;
  max-width: 430px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (min-width: 560px) {
  .container {
    max-width: 530px;
  }
}
@media (min-width: 780px) {
  .container {
    max-width: 750px;
  }
}
@media (min-width: 990px) {
  .container {
    max-width: 960px;
  }
}
@media (min-width: 1250px) {
  .container {
    max-width: 1250px;
  }
}
.ta-c,
.box-btn {
  text-align: center;
}
.d-ib {
  display: inline-block;
}
.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}
.row_no-margin {
  display: flex;
  flex-wrap: wrap;
}
[hidden],
.hidden {
  display: none !important;
}
.fz18 {
  font-size: 16px;
}
@media (min-width: 990px) {
  .fz18 {
    font-size: 18px;
  }
}
.fz20 {
  font-size: 18px;
}
@media (min-width: 990px) {
  .fz20 {
    font-size: 20px;
  }
}
.custom-scrollbar {
  /* for Firefox */
  scrollbar-color: #DC4D41 #008AD3;
  /* цвет ползунка, цвет полосы */
  scrollbar-width: thin;
  /* толщина auto | thin | none  (нужно выбрать один из трёх) */
}
.custom-scrollbar::-webkit-scrollbar-track {
  border-radius: 10px;
  background-color: #F5F5F5;
}
.custom-scrollbar::-webkit-scrollbar {
  width: 12px;
  background-color: #F5F5F5;
}
.custom-scrollbar::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #555;
}
.btn1 {
  display: inline-block;
  width: 100%;
  max-width: 200px;
  padding: 15px 15px 16px;
  border: none;
  cursor: pointer;
  outline: none;
  background: green;
  text-align: center;
  text-decoration: none;
  color: #fff;
  transition-duration: 0.4s;
  transition-property: background;
}
.btn1:hover {
  background: #004d00;
  text-decoration: none;
}
.logo {
  max-width: 200px;
}
.phone {
  font-size: 16px;
  white-space: nowrap;
}
@media (min-width: 990px) {
  .phone {
    font-size: 18px;
  }
}
.toggle {
  display: block;
  width: 34px;
  padding: 4px 5px 0;
  background: transparent;
  border: 0px;
  text-align: center;
  font-size: 1px;
  cursor: pointer;
  outline: none;
}
.toggle span {
  display: inline-block;
  width: 24px;
  height: 3px;
  background: green;
  margin-bottom: 4px;
}
.close {
  display: inline-block;
  position: relative;
  background: none;
  border: 0px;
  outline: none;
  padding: 0;
  cursor: pointer;
  transition: 0.3s color;
  color: green;
}
.close svg,
.close img {
  width: 20px;
  height: 20px;
  fill: currentColor;
  user-select: none;
  pointer-events: none;
}
/*  ФОРМА  */
.form__box-input {
  max-width: 400px;
  margin: 0 auto 12px;
}
.form__box-submit {
  max-width: 400px;
  margin: 0 auto 12px;
}
.form__input {
  width: 100%;
  display: inline-block;
  color: #212121;
  padding: 7px 10px 8px;
  border: 1px solid #212121;
  background: #fff;
  font-family: inherit;
  font-size: 16px;
  outline: none;
}
@media (min-width: 990px) {
  .form__input {
    font-size: 18px;
  }
}
.form__submit {
  -webkit-appearance: none;
  width: 100%;
  border: none;
  font-family: inherit;
}
.form__submit[disabled] {
  background: #004d00;
  cursor: not-allowed;
}
.form__policy {
  display: inline-block;
}
.form__box-checkbox {
  text-align: center;
  margin: 20px auto 0;
  max-width: 300px;
}
.form__checkbox {
  position: absolute;
  left: -9999px;
}
.form__checkbox:checked + .form__checker {
  background: #000;
}
.form__label-checkbox {
  display: block;
  text-align: left;
  position: relative;
  padding-left: 30px;
  cursor: pointer;
}
.form__checker {
  display: inline-block;
  position: absolute;
  left: 0;
  top: 3px;
  width: 14px;
  height: 14px;
  border: 1px solid;
  box-shadow: inset 0 0 0 3px #fff;
  margin-right: 7px;
  background: #fff;
}
/*  МОДАЛЬНОЕ ОКНО  */
.modal {
  position: relative;
  max-width: 480px;
  width: 97vw;
  margin: 0 auto;
  background: #fff;
}
.modal__wrap {
  max-height: calc(100vh - 40px);
  overflow: auto;
  padding: 50px 20px;
}
.modal__close {
  position: absolute;
  z-index: 5;
  top: 20px;
  right: 20px;
}
.lity {
  background: rgba(0, 0, 0, 0.6);
}
.lity-close {
  opacity: 1;
}
/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}
.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}
.slick-list:focus {
  outline: none;
}
.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}
.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}
.slick-track:after {
  clear: both;
}
.slick-loading .slick-track {
  visibility: hidden;
}
.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  outline: none;
  display: none;
}
[dir="rtl"] .slick-slide {
  float: right;
}
.slick-slide img {
  display: block;
}
.slick-slide.slick-loading img {
  display: none;
}
.slick-slide.dragging img {
  pointer-events: none;
}
.slick-initialized .slick-slide {
  display: block;
}
.slick-loading .slick-slide {
  visibility: hidden;
}
.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}
.slick-arrow.slick-hidden {
  display: none;
}
.slider__control {
  max-width: 290px;
  margin: 30px auto 0;
  position: relative;
  display: flex;
  justify-content: space-between;
}
.slider__arrow {
  display: block;
}
/*  ШАПКА  */
.header {
  padding: 15px 0 15px;
  margin: 0 -15px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
}
.header__toggle {
  width: 34px;
  margin: 0 15px;
}
.header__logo {
  width: 200px;
  margin: 0 15px;
}
.header__btns {
  width: 300px;
  margin: 0 15px;
  text-align: center;
}
.header__right {
  width: 200px;
  margin: 0 15px;
  text-align: right;
}
/*  НАВИГАЦИЯ  */
.nav {
  display: none;
  position: fixed;
  z-index: 100;
  width: 100%;
  top: 0;
  left: 0;
  padding: 20px 0 20px;
  background: #fff;
  transform: translateY(-110%);
  opacity: 0;
  transition-duration: 0.5s;
  transition-property: transform, opacity;
}
.nav.js-active {
  opacity: 1;
  transform: translateY(0);
}
.menu__link {
  display: inline-block;
}
/*  Подвал  */
.section {
  position: relative;
  overflow: hidden;
  padding: 3rem 0;
}
.section__title {
  display: block;
  margin: 0 0 20px;
  font-weight: 700;
}
@media screen and (min-width: 1250px) {
  .section {
    padding: 80px 0 80px;
  }
}
.aside {
  position: fixed;
  z-index: 110;
  left: 0;
  top: 0;
  width: 300px;
  height: 100%;
  transform: translateX(-120%);
  opacity: 0;
  transition: 0.5s;
  padding: 60px 0 30px;
  background: #fff;
}
.aside.js-active {
  transform: translateX(0);
  opacity: 1;
}
.aside.js-active + .aside__overlay {
  display: block;
}
.aside + .aside__overlay {
  position: fixed;
  z-index: 109;
  display: none;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
}
.aside__wrap {
  position: relative;
  height: 100%;
  padding: 0 15px;
}
.aside__close {
  position: absolute;
  top: 20px;
  right: 20px;
  color: #212121;
}
.messengers {
  margin: 0 -7px;
}
.messengers__item {
  display: inline-block;
  margin: 0 7px;
  padding: 0;
  width: 36px;
  height: 36px;
  line-height: 32px;
  text-align: center;
  color: #fff;
  transition-duration: 0.4s;
  transition-property: background;
}
.messengers__item:hover {
  background: #212121;
}
.messengers__item svg {
  display: inline-block;
  vertical-align: middle;
  fill: currentColor;
}
.messengers__title {
  font-weight: 300;
  display: inline-block;
  margin: 10px 0 10px 7px;
}
.messengers_telegram {
  background: #4AB3F4;
}
.messengers_whatsapp {
  background: #00E676;
}
.messengers_facebook {
  background: #4AB3F4;
}
.messengers_viber {
  background: #4AB3F4;
}
.my-widget {
  display: block;
  position: fixed;
  z-index: 80;
  right: 25px;
  bottom: 2rem;
  border-radius: 50%;
  width: 2.75rem;
  height: 2.75rem;
  color: #000;
}
@media (min-width: 560px) {
  .my-widget {
    right: 2rem;
  }
}
.my-widget.active .my-widget__open,
.my-widget.active .my-widget__shadow {
  display: none;
}
.my-widget.active .my-widget__close {
  display: flex;
  z-index: 6;
}
.my-widget.active .my-widget__box {
  opacity: 1;
  visibility: visible;
}
.my-widget.active .my-widget__box-1 {
  transform: translateY(-6.5rem);
}
.my-widget.active .my-widget__box-2 {
  transform: translateY(-3.25rem);
}
.my-widget.active .my-widget__box-3 {
  transform: translateY(-9.75rem);
}
.my-widget.active .my-widget__btn {
  opacity: 1;
}
.my-widget__open {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 2.75rem;
  height: 2.75rem;
  background: #f1d823;
  border: 0;
  cursor: pointer;
  outline: none;
  color: #000;
  transition: 0.4s color, 0.4s background;
}
.my-widget__open:hover {
  background: #000;
  color: #f1d823;
}
.my-widget__open svg {
  display: block;
  position: relative;
  z-index: 1;
  fill: currentColor;
}
.my-widget__shadow::after,
.my-widget__shadow::before {
  display: block;
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #f1d823;
  z-index: -1;
}
.my-widget__shadow::before {
  animation: outer-ripple 2000ms linear infinite;
}
.my-widget__shadow::after {
  animation: inner-ripple 2000ms linear infinite;
}
.my-widget__info {
  display: block;
  position: absolute;
  width: 230px;
  right: 0;
  bottom: 3.25rem;
  padding: 20px 25px 20px 20px;
  background: #f1d823;
  color: #000;
  font-weight: 300;
  font-size: 14px;
}
.my-widget__info-close {
  display: block;
  position: absolute;
  z-index: 2;
  top: 10px;
  right: 10px;
  width: 16px;
  height: 16px;
  background: none;
  border: 0;
  cursor: pointer;
  outline: none;
}
.my-widget__info-close::before,
.my-widget__info-close::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 1px;
  background: currentColor;
  left: 0;
  top: 50%;
  transform: rotate(45deg);
}
.my-widget__info-close::after {
  transform: rotate(135deg);
}
.my-widget__close {
  display: none;
  justify-content: center;
  align-items: center;
  width: 2.75rem;
  height: 2.75rem;
  background: #f1d823;
  cursor: pointer;
  border: 0;
  outline: none;
  padding: 0;
  position: relative;
  z-index: 3;
  transition: 0.4s color, 0.4s background;
  color: #000;
}
.my-widget__close:hover {
  background: #000;
  color: #f1d823;
}
.my-widget__close::before,
.my-widget__close::after {
  content: '';
  display: block;
  position: absolute;
  width: 16px;
  height: 2px;
  background: currentColor;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}
.my-widget__close::after {
  transform: translate(-50%, -50%) rotate(135deg);
}
.my-widget__box {
  display: block;
  width: 2.75rem;
  height: 2.75rem;
  position: absolute;
  z-index: 4;
  top: 0;
  left: 0;
  transition: 0.4s transform;
  transform: translateY(0.5rem);
  opacity: 0;
  visibility: hidden;
}
.my-widget__box:hover + .my-widget__label {
  transform: scale(1);
}
.my-widget__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.75rem;
  height: 2.75rem;
  background: #f1d823;
  color: #000;
  transition: 0.4s color, 0.4s background;
  padding: 0;
  border: 0;
  cursor: pointer;
  outline: none;
  position: relative;
  z-index: 4;
}
.my-widget__btn:hover + .my-widget__label {
  transform: scaleX(1);
}
.my-widget__btn svg {
  display: block;
  fill: currentColor;
}
.my-widget__label {
  display: flex;
  align-items: center;
  padding-left: 13px;
  position: absolute;
  z-index: 1;
  right: 100%;
  top: 0;
  height: 100%;
  background: #000;
  color: #fff;
  transition: 0.6s transform;
  transform: scaleX(0);
  transform-origin: 100% 0;
  width: 144px;
  line-height: 1.2;
  border: 1px solid #f1d823;
  font-weight: 100;
  font-size: 14px;
}
@keyframes pulse {
  0% {
    transform: scale(0.8);
    filter: alpha(opacity=50);
    opacity: 0.5;
  }
  10% {
    transform: scale(1.1);
    filter: alpha(opacity=1);
    opacity: 1;
  }
  20% {
    transform: scale(0.9);
    filter: alpha(opacity=1);
    opacity: 1;
  }
  100% {
    transform: scale(0.8);
    filter: alpha(opacity=50);
    opacity: 1;
  }
}
@keyframes outer-ripple {
  0% {
    transform: scale(1);
    filter: alpha(opacity=50);
    opacity: 0.5;
  }
  80% {
    transform: scale(2);
    filter: alpha(opacity=0);
    opacity: 0;
  }
  100% {
    transform: scale(2);
    filter: alpha(opacity=0);
    opacity: 0;
  }
}
@keyframes inner-ripple {
  0% {
    transform: scale(1);
    filter: alpha(opacity=50);
    opacity: 0.5;
  }
  30% {
    transform: scale(1);
    filter: alpha(opacity=50);
    opacity: 0.5;
  }
  100% {
    transform: scale(1.5);
    filter: alpha(opacity=0);
    opacity: 0;
  }
}
.top {
  padding: 0 0 70px;
}
